<template>
    <div>
        <v-header>
            <div slot="right" class="h-right">
                <a v-link="{name:'order-show',params:{id:id}}" class="mg-r10">协议</a></div>
        </v-header>
        <div :class="['partner-box',{'b-alone':standalone}]">
            <div class="warp">
                <span :class="['img avatar tran-all-3',task.sex == 1 ? 'sir' : 'lady']"
                      :style="avatar ? 'background-image:url('+ avatar +')':''"></span>
                <div class="info">
                    <div>
                        <p class="name left">{{task.identityName}}</p>
                        <a href="tel:{{task.phone}}">
                            <span class="phone left">{{task.phone}}</span><span class="img phone left"></span></a>
                    </div>
                    <span class="status">{{task.statusName}}</span>
                    <div class="btns">
                        <input type="button" value="取消" class="cancel button-style" v-if="task.status==0"
                               @click="cancel(task.id,0,task.status,'取消',1)"/>

                        <input type="button" value="提前完结" class="cancel button-style" v-if="task.status==1"
                               @click="cancel(task.id,0,task.status,'提前完结',1)"/>

                        <input type="button" value="撤回" class="cancel button-style"
                               v-if="task.operater == user_id && (task.status==2||task.status==3)"
                               @click="commitBack(task.id,0,task.status,1)"/>

                        <span class="show-remark"
                              v-if="(task.status==2 || task.status==3) && task.remark"
                              @click="showRemark(task.remark,task.status)">
                查看原因
                </span>
                    </div>
                </div>
            </div>
            <div class="notice" v-if="task.oweDate && task.role == 1">
                <p class="looks">提示:你的账户资金已不足,任务将于{{task.oweDate}}提前完结,请尽快
                    <span style="color:#71bec4;" v-link="{name:'recharge',query:{from:'/task/detail'}}">充值</span></p>
            </div>
            <div class="notice" v-if="task.oweDate && task.role == 0">
                <p class="looks">提示:对方的账户资金已不足,任务将于{{task.oweDate}}提前完结</p>
            </div>
        </div>
        <v-content :type="'task-detail'">
            <ul class="task-line">
                <li v-for="item in items"
                    :class="['task-box',(item.status==0 || item.status == -1 || item.status == 3) ? '':'hasOpted']">
                    <span class="fhour">{{item.finishHour}}h</span>
                    <span class="line" v-if="$index < (items.length-1)"></span>
                    <span class="img line-point"></span>
                    <div class="task-item">
                        <div class="title">
                            <span class="date">{{item.date | dateFmtYM}}</span>
                            <span class="name">{{item.serviceName}}</span>
                            <span :class="['task-tip',item.statusClazz,(item.status==2||item.status==3)?'opa-6':'']">
                  {{item.statusName}}</span>
                            <span @click="showRemark(item)" :class="['img','help01']"
                                  v-if="(item.status == 2 || item.status== 4) && item.remark"></span>
                            <span @click="showRemark(item)" :class="['img','help02']"
                                  v-if="(item.status == -1 || item.status== 3) && item.remark"></span>
                        </div>
                        <p class="time">{{item.workTime}}</p>
                        <div :class="['btns',$index == (items.length-1)?'last':'']">
                            <input type="button" value="取消" class="cancel button-style"
                                   v-if="item.status==0 && (task.status!=2 && task.status!=3)"
                                   @click="cancel(item.id,$index,item.status,'取消',2)"/>

                            <input type="button" value="提前完结" class="cancel button-style"
                                   v-if="item.status==1 && (task.status!=2 && task.status!=3)"
                                   @click="cancel(item.id,$index,item.status,'提前完结',2)"/>

                            <input type="button" value="完结" class="cancel button-style"
                                   v-if="item.status==6 && task.status == 1"
                                   @click="cancel(item.id,$index,item.status,'完结',2)"/>

                            <input type="button" value="撤回" class="cancel button-style"
                                   v-if="item.operater == user_id && (item.status==2||item.status==3) && task.status!=2 && task.status!=3"
                                   @click="commitBack(item.id,$index,item.status,2)"/>

                            <input type="button" value="撤回" class="cancel button-style" style="color: #ccc;"
                                   v-if="task.status==2 || task.status==3 && (item.status==2||item.status==3)"/>

                            <span class="show-pay" v-if="item.status==4 || item.status==5"
                                  v-link="{name:'capital-detail',query:{taskId:item.id}}">
                ￥{{item.costMoney}}
                </span>

                            <!--<span class="show-remark"-->
                            <!--v-if="item.remark"-->
                            <!--@click="showRemark(item.remark,item.status)">-->
                            <!--查看原因-->
                            <!--</span>-->
                        </div>
                    </div>
                </li>
            </ul>
        </v-content>
        <div class="task-process">
            <div class="warp">
                <ul class="dis-flex process">
                    <li class="flex-1 info">
                        <p>服务单价：<span class="price">￥<span class="font-14">{{task.price}}</span></span></p>
                        <p>已完成：&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="hour font-14">{{task.finishedHours}}H</span>
                        </p>
                        <p>待完成：&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="hour font-14">{{task.unfinishedHours}}H</span>
                        </p>
                    </li>
                    <li class="flex-1" style="line-height: .5rem;">
                        <p>已结算：<span class="price">￥<span class="font-14">{{task.finishedMoney}}</span></span></p>
                    </li>
                </ul>
            </div>
        </div>
    </div>
</template>
<script>
    import VHeader from '../../components/Header'
    import VContent from '../../components/Content'
    import {lt, url} from '../../const'
    import {DJUtils} from '../../assets/js/common'
    import {toast} from '../../assets/js/toast'
    import {imgHost} from '../../filter'
    import $ from 'jquery'

    export default {
        route: {
            data({to:{params}}){
                this.id = params.id;
                return this.$http.get(url.taskDetail, {id: this.id}).then(
                    function ({data:{success, data, resultMsg}}) {
                        if (success) {
                            this.user_id = lt.getJson(lt.keys.user).userId;
                            this.items = [];
                            let items = data.items;
                            for (let i in items) {
                                items[i].statusClazz = this.setStatusClazz(items[i].status);
                                this.items.push(items[i]);
                            }
                            data.items = [];
                            if (data.portrait) {
                                this.avatar = imgHost(data.portrait);
                            }
                            this.task = data;
                            this.$nextTick(function () {
                                this.resizeContent();
                            })
                        } else {
                            toast.alert(resultMsg);
                        }
                    }, function (response) {
                        toast.alert('网络异常');
                    }
                );
            }
        },
        ready(){
            this.standalone = DJUtils.isStandalone();
        },
        data () {
            return {
                id: 0,
                avatar: '',
                user_id: 0,
                statusClazz: ['cancel', 'be-doing', 'doing', 'ahead', 'done', 'wait-done'],
                task: {},
                items: [],
                optTask: {},
                standalone: false
            }
        },
        methods: {
            showRemark(item){
                let status = parseInt(item.status);
                let title;
                if (status == -1 || status == 3) {
                    title = '取消原因';
                } else if (status == 2 || status == 4) {
                    title = '提前完结原因';
                }
                toast.message(item.remark, title, item.optName, item.optDate);
            },
            cancel(id, index, status, msg, optObj){
                this.optTask = {id: id, index: index, status: status, optObj: optObj};
                if (status == 6) {
                    this.commitConfirm();
                } else {
                    toast.commit(this.doCommit, `请说明${msg}原因`, `请填写${msg}原因`);
                }
            },

            commitConfirm (){
                let _url = url.manualFinish;
                let conformTitle = '确定完结';
                let vm = this;
                toast.conform(conformTitle, function () {
                    vm.$http.get(_url,
                        {
                            id: vm.optTask.id,
                            operateObj: vm.optTask.optObj
                        }).then(
                        function ({data:{success, data, resultMsg}}) {
                            if (success) {
                                toast.alert("操作成功");
                                vm.reloadTask();
                            } else {
                                toast.alert(resultMsg);
                            }
                        }
                    );
                });
            },
            commitBack(id, index, status, optObj){
                let vm = this;
                let msg = (status == 2 ? '提前完结' : '取消');
                toast.conform(`确定撤回${msg}操作吗？`, function () {
                    vm.doBack(id, index, optObj);
                });
            },
            doCommit(content){
                if (!content) {
                    toast.alert('内容不能为空');
                    return;
                }
                let _url;
                if (this.optTask.status == 0) {
                    _url = url.taskCancel;
                } else if (this.optTask.status == 1) {
                    _url = url.taskFinish;
                } else if (this.optTask.status == 6) {
                    _url = url.manualFinish;
                }
                this.$http.get(_url,
                    {
                        id: this.optTask.id,
                        remark: content,
                        operateObj: this.optTask.optObj
                    }).then(
                    function ({data:{success, data, resultMsg}}) {
                        toast.doCommit();
                        if (success) {
                            toast.alert("操作成功");
                            data.statusClazz = this.setStatusClazz(data.status);
                            this.reloadTask();
                        } else {
                            toast.alert(resultMsg);
                        }
                    }
                );
            },
            doBack(id, index, optObj){
                this.$http.get(url.taskBack,
                    {
                        id: id,
                        operateObj: optObj
                    }).then(
                    function ({data:{success, data, resultMsg}}) {
                        if (success) {
                            toast.alert("撤回成功");
                            data.statusClazz = this.setStatusClazz(data.status);
                            if (optObj == 1) {
                                this.reloadTask();
                            } else {
                                this.items.$set(index, data);
                            }
                        } else {
                            toast.alert(resultMsg);
                        }
                    }
                );
            },
            reloadTask(){
                this.$http.get(url.taskDetail, {id: this.id}).then(
                    function ({data:{success, data, resultMsg}}) {
                        if (success) {
                            this.items = [];
                            let items = data.items;
                            for (let i in items) {
                                items[i].statusClazz = this.setStatusClazz(items[i].status);
                                this.items.push(items[i]);
                            }
                            data.items = [];
                            if (data.portrait) {
                                this.avatar = imgHost(data.portrait);
                            }
                            this.task = data;
                            this.$nextTick(function () {
                                this.resizeContent();
                            })
                        } else {
                            toast.alert(resultMsg);
                        }
                    }, function (response) {
                        toast.alert('网络异常');
                    }
                );
            },
            resizeContent(){
                let notice = $(".partner-box .notice").height();
                $(".content.task-detail").css('top', (44 + 75 + notice) + 'px');
                $(".c-alone.task-detail").css('top', (64 + 75 + notice) + 'px');
            },
            setStatusClazz(status){
                if (status == 2) {
                    status = 4;
                }
                if (status == 3) {
                    status = -1;
                }
                switch (status) {
                    case -1:
                        return this.statusClazz[0];
                    case 0:
                        return this.statusClazz[1];
                    case 1:
                        return this.statusClazz[2];
                    case 4:
                        return this.statusClazz[3];
                    case 5:
                        return this.statusClazz[4];
                    case 6:
                        return this.statusClazz[5];
                    default:
                        return '';
                }
            }
        },
        components: {
            VHeader,
            VContent
        }
    }
</script>
